import React, { useState } from "react";

function PaymentPopup({
  activeTab,
  popupData,
  p_quantity,
  setP_Quantity,
  errorMessage,
  setErrorMessage,
  handlePayment,
  closePopup,
}) {
  //    
  const [paymentMethod, setPaymentMethod] = useState(""); // : 

  //    
  if (activeTab !== "p_List" || !popupData) return null;

  //    
  const handlePaymentMethodChange = (e) => {
    setPaymentMethod(e.target.value);
  };

  // handlePayment paymentMethod   
  const handlePaymentWithMethod = () => {
    if (p_quantity === 0) {
      setErrorMessage(" 0   .");
      return;
    }
    if (p_quantity > popupData.GOODQUANTITY) {
      setErrorMessage(
        "(" + popupData.GOODQUANTITY + ")  ."
      );
      return;
    } else {
      setErrorMessage("");
    }
    handlePayment(paymentMethod);
  };

  const handleP_QuantityChange = (e) => {
    const newQuantity = e.target.value === "" ? "" : Number(e.target.value);
    setP_Quantity(newQuantity);

    if (newQuantity !== "" && newQuantity > popupData?.GOODQUANTITY) {
      setErrorMessage(
        `(${popupData.GOODQUANTITY})   .`
      );
    } else {
      setErrorMessage("");
    }
  };

  const priceToUse =
    popupData.DISCOUNTED_PRICE !== popupData.PRICE
      ? popupData.DISCOUNTED_PRICE
      : popupData.PRICE;

  const handleOverlayClick = (e) => {
    if (e.target === e.currentTarget) {
      closePopup(); //     
    }
  };
  return (
    <div className="popup" onClick={handleOverlayClick}>
      <div>
        <h2>{popupData.PRODUCTNAME}</h2>
        <p> : {popupData.PRODUCTCODE}</p>
        <p>: {priceToUse.toLocaleString()} </p>
        <p>
          :
          <input
            type="number"
            value={p_quantity}
            min="1"
            onChange={handleP_QuantityChange}
          />
          <b>  </b>(<b className="qSize">{popupData.GOODQUANTITY}</b>
          <b className="qMessage"> </b>)
        </p>
        {errorMessage && <p className="error-message">{errorMessage}</p>}

        {/*    */}
        <p>
           :
          <select
            className="pay-sel"
            value={paymentMethod}
            onChange={handlePaymentMethodChange}
          >
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
          </select>
        </p>
        {/*    */}
        <p> : {(p_quantity * priceToUse).toLocaleString()} </p>
        {/* :     / */}
        <div className="popup-buttons">
          <button
            className="pay-button"
            onClick={handlePaymentWithMethod}
            disabled={!!errorMessage}
          >
            
          </button>
          <button className="cancel-button" onClick={closePopup}>
            
          </button>
        </div>
      </div>
    </div>
  );
}

export default PaymentPopup;
