import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import "../css/MainPage.css";
import LoginModal from "./LoginModal"; // LoginModal  

function MainPage({ setLoginModalOpen }) {
  const [isLoggedIn, setIsLoggedIn] = useState(false); //   
  const [username, setUsername] = useState(""); //     
  const [isModalOpen, setIsModalOpen] = useState(false); //   
  const navigate = useNavigate();

  //    
  useEffect(() => {
    const storedUsername = getCookie("username");
    if (storedUsername) {
      setIsLoggedIn(true); //  username    
      setUsername(storedUsername); //   
    }
  }, []);

  //    
  const getCookie = (name) => {
    const value = `; ${document.cookie}`;
    const parts = value.split(`; ${name}=`);
    if (parts.length === 2) return parts.pop().split(";").shift();
    return null;
  };

  //     
  const handleLoginClick = () => {
    setIsModalOpen(true); //   
  };

  //     
  const handleLogoutClick = () => {
    //    
    document.cookie = "username=; path=/; max-age=0"; // username  
    setIsLoggedIn(false); //   
    setUsername(""); //   
    navigate("/"); //   
    alert(".");
  };

  //     
  const handleMenuClick = (menu) => {
    if (menu === "Inventory") {
      navigate("/Inventory"); //       
    }
    if (menu === "SalesPage") {
      navigate("/SalesPage"); //       
    }
    if (menu === "hr") {
      navigate("/hr"); //       
    }
  };

  return (
    <div className="main-page">
      <header>
        <span className="logo-img">
          <img
            src={process.env.PUBLIC_URL + "/erp_logo.png"}
            width="120px"
            height="78px"
            alt="logo-image"
          />
        </span>
        {/*      */}
        {!isLoggedIn ? (
          <button className="login-btn" onClick={handleLoginClick}>
            
          </button>
        ) : (
          <div className="logged-in-info">
            <span>, {username}!</span>
            <button className="logout" onClick={handleLogoutClick}>
              
            </button>
          </div>
        )}
      </header>

      <div className="menu-container">
        <div className="menu-item" onClick={() => handleMenuClick("Inventory")}>
          
        </div>
        <div className="menu-item" onClick={() => handleMenuClick("SalesPage")}>
          
        </div>
        <div className="menu-item" onClick={() => handleMenuClick("hr")}>
          
        </div>
      </div>

      <aside>
        <h2></h2>
        <ol>
          <li> 1</li>
          <li> 2</li>
          <li> 3</li>
        </ol>
      </aside>

      {/*    true   */}
      {isModalOpen && (
        <LoginModal
          isOpen={isModalOpen}
          onClose={() => setIsModalOpen(false)}
          setLoginStatus={setIsLoggedIn}
          setUsername={setUsername}
        />
      )}
    </div>
  );
}

export default MainPage;
