import React, { useState } from "react";

function AddStockForm({ setRefreshTrigger, closeAddTab }) {
  const getStringDate = (date) => date.toISOString().slice(0, 10);
  const [date, setDate] = useState(getStringDate(new Date()));
  const [productCode, setProductCode] = useState(""); //    
  const [productName, setProductName] = useState("");
  const [cost, setCost] = useState("");
  const [quantity, setQuantity] = useState("");
  const [categoryID, setCategoryID] = useState("1");

  //     
  const handleCodeGenerate = async () => {
    try {
      const response = await fetch("http://localhost:84/generateCode", {
        method: "GET",
      });
      if (!response.ok) throw new Error("   ");
      const newProductCode = await response.text(); //    
      setProductCode(newProductCode);
    } catch (error) {
      // console.error("     :", error);
      alert("   .");
    }
  };

  const handleAddSubmit = async (e) => {
    e.preventDefault();
    const formData = {
      PRODUCTCODE: productCode,
      PRODUCTNAME: productName,
      CATEGORYID: categoryID,
      COST: cost,
      TOTALQUANTITY: quantity,
      LASTUPDATED: date,
      TRANSACTIONTYPE: "IN", // addInOut 
      QUANTITY: quantity, // addInOut 
      TRANSACTIONDATE: date, // addInOut 
      NOTE: "  ", // addInOut 
    };

    try {
      const response = await fetch("http://localhost:84/add", {
        method: "POST",
        headers: {
          "Content-Type": "application/x-www-form-urlencoded;charset=UTF-8",
        },
        body: new URLSearchParams(formData).toString(),
      });

      if (!response.ok) throw new Error("  ");

      const result = await response.json();
      if (result.success) {
        alert("  .");
        setRefreshTrigger((prev) => prev + 1);
        closeAddTab();
      } else {
        throw new Error(result.message);
      }
    } catch (error) {
      // console.error("  :", error);
      alert("  : " + error.message);
    }
  };

  return (
    <div className="addStockArea">
      <form onSubmit={handleAddSubmit}>
        <h2></h2>
        <label>
          <span>:</span>
          <input name="PRODUCTCODE" value={productCode} required disabled />
          <button
            className="codeBtn"
            type="button"
            onClick={handleCodeGenerate}
          >
            
          </button>
        </label>
        <label>
          :
          <input
            name="PRODUCTNAME"
            value={productName}
            onChange={(e) => setProductName(e.target.value)}
            required
          />
        </label>
        <label>
          <span>:</span>
          <select
            name="CATEGORYID"
            value={categoryID}
            onChange={(e) => setCategoryID(e.target.value)}
            required
          >
            <option value="1"></option>
            <option value="2"></option>
            <option value="3"></option>
          </select>
        </label>
        <label>
          <span>:</span>
          <input
            name="COST"
            type="number"
            value={cost}
            onChange={(e) => setCost(e.target.value)}
            required
          />
        </label>
        <label>
          <span> :</span>
          <input
            name="TOTALQUANTITY"
            type="number"
            value={quantity}
            onChange={(e) => setQuantity(e.target.value)}
            required
          />
        </label>
        <label>
          <span> :</span>
          <input
            name="LASTUPDATED"
            type="date"
            value={date}
            onChange={(e) => setDate(e.target.value)}
            required
          />
        </label>
        <button type="submit"></button>
        <button type="button" onClick={closeAddTab}>
          
        </button>
      </form>
    </div>
  );
}

export default AddStockForm;
