import React from "react";

const EditEmployeeForm = ({
  formData,
  handleInputChange,
  errors,
  handleSave,
  handleCancel,
  positions,
  formRef,
}) => {
  //  YYYY-MM-DD   
  function formatDate(date) {
    const d = new Date(date);
    const year = d.getFullYear();
    const month = (d.getMonth() + 1).toString().padStart(2, "0"); //  0  +1
    const day = d.getDate().toString().padStart(2, "0");
    return `${year}-${month}-${day}`;
  }

  // formData.regDate      
  const formattedReg = formatDate(formData.regDate);
  const formattedMod = formatDate(formData.modDate);
  return (
    <div className="employee-form" ref={formRef}>
      <h2>  </h2>
      <form>
        <div className="form-row">
          <label>:</label>
          <input
            type="text"
            name="employeeId"
            value={formData.employeeId}
            readOnly
          />
          <span className="required"></span>
        </div>
        <div className="form-row">
          <label>:</label>
          <input
            type="text"
            name="name"
            value={formData.name}
            onChange={handleInputChange}
          />
          <span className="required">{errors.name || ""}</span>
        </div>
        <div className="form-row">
          <label>:</label>
          <input
            type="text"
            name="phone"
            value={formData.phone}
            onChange={handleInputChange}
          />
          <span className="required">{errors.phone || ""}</span>
        </div>
        <div className="form-row">
          <label>:</label>
          <select
            name="department"
            value={formData.department}
            onChange={handleInputChange}
          >
            <option value=""> </option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
          </select>
          <span className="required">{errors.department || ""}</span>
        </div>
        <div className="form-row">
          <label>:</label>
          <select
            name="position"
            value={formData.position}
            onChange={handleInputChange}
          >
            <option value=""> </option>
            {positions.map((position) => (
              <option key={position} value={position}>
                {position}
              </option>
            ))}
          </select>
          <span className="required">{errors.position || ""}</span>
        </div>
        <div className="form-row">
          <label>:</label>
          <input
            type="date"
            name="regDate"
            value={formattedReg}
            onChange={handleInputChange}
          />
          <span className="required">{errors.regDate || ""}</span>
        </div>
        <div className="form-row">
          <label>:</label>
          <input
            type="date"
            name="modDate"
            value={formattedMod}
            onChange={handleInputChange}
          />
          <span className="required">{errors.modDate || ""}</span>
        </div>
        <div className="form-row">
          <label> :</label>
          <div className="radio-group">
            <label>
              <input
                type="radio"
                name="employmentStatus"
                value="A"
                checked={formData.employmentStatus === "A"}
                onChange={handleInputChange}
              />
              
            </label>
            <label>
              <input
                type="radio"
                name="employmentStatus"
                value="L"
                checked={formData.employmentStatus === "L"}
                onChange={handleInputChange}
              />
              
            </label>
            <label>
              <input
                type="radio"
                name="employmentStatus"
                value="R"
                checked={formData.employmentStatus === "R"}
                onChange={handleInputChange}
              />
              
            </label>
          </div>
          <span className="required">{errors.employmentStatus || ""}</span>
        </div>
        <div className="form-buttons">
          <button className="add-btn" onClick={handleSave}>
            
          </button>
          <button className="cancel-btn" onClick={handleCancel}>
            
          </button>
        </div>
      </form>
    </div>
  );
};

export default EditEmployeeForm;
