import React from "react";
import { useNavigate, useLocation } from "react-router-dom";

import "../css/Header.css";

function Header() {
  const navigate = useNavigate();
  const location = useLocation(); //   

  //   
  const getActiveTab = () => {
    switch (location.pathname) {
      case "/inventory":
        return "inventory";
      case "/SalesPage":
        return "SalesPage";
      case "/hr":
        return "hr";
      default:
        return "inventory";
    }
  };

  const goMain = () => {
    navigate("/");
  };

  //  
  const handleLogout = () => {
    document.cookie = "username=; path=/; max-age=0"; // username  
    alert(".");
    navigate("/"); //     
  };

  return (
    <header className="header">
      <button className="logo-btn" onClick={goMain}>
        <div className="logo-wrapper">
          <img
            src={process.env.PUBLIC_URL + "/erp_logo.png"}
            width="120px"
            height="78px"
            alt="logo-image"
          />
          <span className="tooltip"> </span>
        </div>
      </button>
      <nav className="header-nav">
        <button
          className={getActiveTab() === "inventory" ? "active" : ""}
          onClick={() => navigate("/inventory")}
        >
          
        </button>
        <button
          className={getActiveTab() === "SalesPage" ? "active" : ""}
          onClick={() => navigate("/SalesPage")}
        >
          
        </button>
        <button
          className={getActiveTab() === "hr" ? "active" : ""}
          onClick={() => navigate("/hr")}
        >
          
        </button>
        <button className="logout" onClick={handleLogout}>
          
        </button>
      </nav>
    </header>
  );
}

export default Header;
